* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 15px;
}

img {
  width: 50rem;
  height: 40rem;
}

.portrait {
  max-width: 350px;
  max-height: 250px;
  float: left;
}

.nav {
  width: 100%;
  margin: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  background-color: rgb(243, 228, 227);
  padding-bottom: 5px;
}

.nav h1 {
  font-size: 5rem;
  text-align: center;
  margin-top: 10px;
}

.nav h2 {
  font-size: 3rem;
  text-align: center;
}

.links {
  margin-right: 10px;
}

main {
  position: relative;
  top: 14rem;
}

a {
  text-decoration: none;
  color: black;
  font-size: 2rem;
  font-weight: 100;
}

a:hover {
  color: #f26d9e;
}

p {
  font-size: 1rem;
  padding: 1rem 2rem;
}

.contact {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
}

.navBottom {
  display: flex;
  justify-content: space-between;
  margin: 2rem 2rem 0 2rem;
}

.icon {
  height: 1.5rem;
  width: 1.5rem;
  margin: 0 0.5rem;
}

.icon:hover {
  height: 2rem;
  width: 2rem;
}

.projectLinks {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.projectContainer {
  margin: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.imageAndHover {
  position: relative;
}

.projectImage {
  border-radius: 1rem;
}

.hoverDiv {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(240, 233, 233);
  width: 50rem;
  /* height: 15rem; */
  border-radius: 1rem 1rem 0 0;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  padding-left: 2rem;
  padding-right: 2rem;
  height: 0;
  -webkit-transition: height 200ms ease-in;
  -moz-transition: height 200ms ease-in;
  -o-transition: height 200ms ease-in;
  transition: height 200ms ease-in;
}

.hoverDiv p {
  font-size: 1.5rem;
  color: black;
  font-weight: bold;
}

.imageAndHover:hover .hoverDiv {
  opacity: 0.9;
  height: 15rem;
}

.about {
  width: 80%;
  margin: 2rem auto;
}

.about p {
  font-size: 1.5rem;
  font-weight: 150;
  word-wrap: break-word;
}

.projectsContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.welcomeBanner {
  background-color: rgb(243, 228, 227);
  margin-top: 0;
  padding: 4rem 2rem;
}

.welcomeBanner p {
  font-size: 2rem;
  font-weight: 100;
}

/* MEDIA QUERIES */
@media only screen and (max-width: 600px) {
  img {
    width: 20rem;
    height: 15rem;
  }
  .hoverDiv {
    width: 20rem;
    height: 5rem;
    opacity: 0.9;
    padding: 0.5rem;
    border-radius: 0 0 1rem 1rem;
    top: 10rem;
  }
  .hoverDiv p {
    font-size: 0.8rem;
    padding: 0.1rem;
  }
  .hoverDiv:hover {
    transition: none;
    height: 5rem;
  }
  .nav h1 {
    font-size: 3rem;
  }

  .nav h2 {
    font-size: 1.5rem;
  }

  a,
  .welcomeBanner p {
    font-size: 1.5rem;
  }

  .navBottom {
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 7rem;
  }
}

@media only screen and (max-width: 400px) {
  .navBottom {
    margin-top: -0.5rem;
  }
}
